.theia-progress-bar-container {
  width: 100%;
  height: 2px;
}

.theia-progress-bar {
  background-color: var(--theia-progressBar-background);
  height: 2px;
  width: 3%;
  animation: progress-animation 1.3s 0s infinite
    cubic-bezier(0.645, 0.045, 0.355, 1);
}

@keyframes progress-animation {
  0% {
    margin-left: 0%;
    width: 3%;
  }
  60% {
    margin-left: 45%;
    width: 20%;
  }
  100% {
    margin-left: 99%;
    width: 1%;
  }
}
